import { create } from 'zustand';
import { persist } from 'zustand/middleware';

export interface OrderItem {
  id: number;
  title: string;
  price: number;
  description: string;
  category: string;
  image: string;
  rating: {
    rate: number;
    count: number;
  };
  quantity: number;
}

export interface Order {
  id: string;
  orderNumber: string;
  orderDate: string;
  orderTime: string;
  estimatedDelivery: string;
  items: OrderItem[];
  subtotal: number;
  tax: number;
  shipping: number;
  total: number;
  paymentMethod: string;
  shippingAddress: {
    name: string;
    address: string;
    city: string;
    state: string;
    zipCode: string;
    country: string;
  };
}

interface OrderStore {
  orders: Order[];
  currentOrder: Order | null;
  addOrder: (order: Order) => void;
  clearCurrentOrder: () => void;
  getOrderById: (orderId: string) => Order | undefined;
}

export const useOrderStore = create<OrderStore>()(
  persist(
    (set, get) => ({
      orders: [],
      currentOrder: null,
      addOrder: (order: Order) => {
        set({
          orders: [...get().orders, order],
          currentOrder: order
        });
      },
      clearCurrentOrder: () => {
        set({
          currentOrder: null
        });
      },
      getOrderById: (orderId: string) => {
        return get().orders.find(order => order.id === orderId);
      }
    }),
    {
      name: 'order-storage',
    }
  )
);
